.about{
    padding-bottom: 48px;
    height: 100vh;
}

.about-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
}

.picture{
    height: 420px;
}

.about-info{
    font-size: 24px;
}

.info-name{
    color: blue;
    font-size: 32px;
}

@media only screen and (max-width: 720px){
    .about-top{
        display: flex;
        flex-direction: column;
    }

    .picture{
        height: 280px;
    }

    .about-info{
        font-size: 20px;
    }

    .info-name{
        font-size: 26px;
    }
}